<template>
    <v-expansion-panel>
        <v-expansion-panel-content>
            <div slot="header" class="pa-2">
                <v-badge right :color="GET_ALERTS ? 'error' : 'success'">
                    <v-icon
                        slot="badge"
                        small
                        v-if="GET_ALERTS"
                    >
                        fas fa-exclamation
                    </v-icon>
                    <v-icon v-else
                        slot="badge"

                    >
                        fas fa-check
                    </v-icon>
                    <span
                        style="font-size: 15pt;"
                    >
                        <v-icon>
                            fas fa-bells
                        </v-icon>
                        Alerts
                    </span>
                </v-badge>
            </div>
            <v-card
                :color="GET_ALERTS ? 'error' : 'success'"
                class="pa-2"
            >
                <v-data-table
                    :headers="headers"
                    :items="list"
                    :pagination.sync="pagination"
                >
                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.date }}</td>
                        <td>{{ props.item.time }}</td>
                        <td>{{ props.item.name }}</td>
                        <td>{{ props.item.alert }}</td>
                        <td>{{ props.item.text }}</td>
                        <td>{{ props.item.level }}</td>
                    </template>
                </v-data-table>
            </v-card>
        </v-expansion-panel-content>
    </v-expansion-panel>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  name: 'AlarmList',
  computed: {
    ...mapState({
      list: 'alertsList',
    }),
    ...mapGetters([
      'GET_ALERTS',
    ]),
  },
  mounted() {
    this.getAlerts();
  },
  data: () => ({
    alarmPresent: false,
    headers: [
      { text: 'Date', align: 'left', value: 'date' },
      { text: 'Time', align: 'left', value: 'time' },
      { text: 'Device Name', value: 'name' },
      { text: 'Alert Subject', value: 'subject', sortable: false },
      { text: 'Alert Message', value: 'message', sortable: false },
      { text: 'Alert Level', value: 'level', sortable: false },
    ],
    pagination: {
      descending: false,
      page: 2,
      rowsPerPage: 5,
    },
  }),
  methods: {
    ...mapActions({
      getAlerts: 'GET_EVENTS',
    }),
  },
};

</script>
